import { ICommerceApiSettings } from '@msdyn365-commerce/core-internal';

/**
 * Generates a cache key from the given ApiSettings object
 */
export const getCacheKey = (apiSettings: ICommerceApiSettings) => {
    if (!apiSettings) {
        return '';
    }

    return `${apiSettings.channelId}-${apiSettings.oun}-${apiSettings.catalogId}`;
};
